@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;500&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Roboto Slab', serif;
}

html,
body {
  margin: 0;
  background-color: #121212;
}

#root {
  --swiper-theme-color: white;
}

.GameContent {
  padding: 0px 32px 32px 32px;
  max-width: 1024px;
  margin: auto;
}

.GameContent h4 {
  color: #333333;
  margin: 1.5em 0;
}

.GameContent p {
  line-height: 1.5em;
}

.GameContentDetails {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.GameContentDetails div {
  width: 25%;
  padding-right: 16px;
}

@media (max-width: 1024px) {
  .GameContentDetails div {
    width: 50%;
  }
}

@media (max-width: 480px) {
  .GameContentDetails div {
    width: 100%;
  }
}

.GameContentDetails p {
  margin: 0;
}

.GameHeader {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  height: 500px;
  z-index: 1;
  padding: 0px 32px;
}

.GameHeaderBackground {
  background: no-repeat top;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: -2;
}

.GameHeaderBackgroundFade {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 80%,
    95%,
    #121212
  );
  z-index: -1;
}

.GameHeader h1 {
  text-align: center;
  font-size: 5em;
  text-shadow: 0 0 0.1em #121212;
  margin: 0;
}

@media (max-width: 768px) {
  .GameHeader h1 {
    font-size: 4em;
  }
}

@media (max-width: 480px) {
  .GameHeader h1 {
    font-size: 3em;
  }
}

.GameHeaderPlatforms {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.GameHeaderPlatforms span {
  line-height: 0;
  text-shadow: 0 0 0.1em #121212;
}

.GameHeaderPlatforms img {
  height: 1.2em;
  -webkit-filter: drop-shadow(0 0 0.1em #121212);
          filter: drop-shadow(0 0 0.1em #121212);
}

.GameHeaderAdditionalInformation {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 32px;
  gap: 32px;
}

.GameHeaderFirstReleaseDate {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 4px;
  padding: 2px 8px;
  background-color: white;
  color: black;
  font-weight: 500;
  box-shadow: 0 0 2px black;
  font-size: 1em;
}

.GameMedia {
  padding: 16px 0;
}

.swiper-container {
  width: 100%;
  height: 100%;
  height: calc(23.8vw * (9 / 16));
}

@media (max-width: 768px) {
  .swiper-container {
    height: calc(45.875vw * (9 / 16));
  }
}

.swiper-wrapper {
  -webkit-align-items: center;
          align-items: center;
}

.swiper-slide {
  display: block;
  border-radius: 8px;
  overflow: hidden;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.Game {
  color: white;
  min-height: 100vh;
  background: #121212;
}

.GameSearchIcon {
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  height: 24px;
  width: 24px;
  margin: 16px;
  padding: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
}

.GameSearchIcon:hover {
  cursor: pointer;
}

.GameSearchIcon:hover img {
  opacity: 1;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #5e5e5e);
          filter: drop-shadow(0 0 0.1em #5e5e5e);
}

.GameSearchIcon img {
  height: 24px;
  width: 24px;
  opacity: 0.75;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #9e9e9e);
          filter: drop-shadow(0 0 0.1em #9e9e9e);
}

.GameBackIcon {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  height: 24px;
  width: 24px;
  margin: 16px;
  padding: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
}

.GameBackIcon:hover {
  cursor: pointer;
}

.GameBackIcon:hover img {
  opacity: 1;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #5e5e5e);
          filter: drop-shadow(0 0 0.1em #5e5e5e);
}

.GameBackIcon img {
  height: 24px;
  width: 24px;
  opacity: 0.75;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #9e9e9e);
          filter: drop-shadow(0 0 0.1em #9e9e9e);
}

.Landing {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Landing input {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  outline: none;
  width: 30vw;
  padding: 15px;
  border: 0px;
  background-color: #e6e6e6;
  font-weight: 500;
  border-radius: 9999px;
  box-shadow: 0 0 4px 1px #e6e6e6;
  text-align: center;
  text-overflow: ellipsis;
}

.SearchResult {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 500;
}

table {
  border-radius: 20px;
  border-spacing: 0.5rem;
  border-collapse: collapse;
  background-color: #e6e6e6;
  box-shadow: 0 0 4px 1px #e6e6e6;
  overflow: hidden;
}

th {
  background: #cccccc;
}

td,
th {
  padding: 24px 48px;
  text-align: left;
}

tbody tr:hover {
  cursor: pointer;
  background: #d9d9d9;
}

.SearchResultSearchIcon {
  height: 24px;
  width: 24px;
  margin: 16px;
  padding: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
}

.SearchResultSearchIcon:hover {
  cursor: pointer;
}

.SearchResultSearchIcon:hover img {
  opacity: 1;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #5e5e5e);
          filter: drop-shadow(0 0 0.1em #5e5e5e);
}

.SearchResultSearchIcon img {
  height: 24px;
  width: 24px;
  opacity: 0.75;
  transition: 0.2s;
  -webkit-filter: drop-shadow(0 0 0.1em #9e9e9e);
          filter: drop-shadow(0 0 0.1em #9e9e9e);
}

